.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 16px;
  background: #fff;
  border-radius: 16px;
  width: 100%;
  max-width: 320px;
}

.title {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 12px;
  color: #333;
}

.input {
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-bottom: 16px;
  width: 100%;
  text-align: center;
  font-size: 1rem;
}

.qrcode {
  margin-bottom: 12px;
  border-radius: 8px;
}

.payload {
  font-size: 0.7rem;
  word-break: break-all;
  color: #666;
  text-align: center;
  box-shadow: 0 8px 16px rgba(0,0,0,0.15);
  padding: .5em;
  cursor: pointer;
}
